@font-face {
	font-family: 'Airport';
	src: local('Airport'), url('./fonts/Airport.otf') format('opentype');
}
@font-face {
	font-family: 'AlegreSans-Regular';
	src: local('AlegreSans-Regular'),
		url('./fonts/AlegreSans-Regular.ttf') format('truetype');
}
* {
	overflow: visible;
}
.App-logo {
	height: 40vmin;
	pointer-events: none;
}
@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	min-height: 10vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}

.App-main {
	background-color: white;
	min-height: 90vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: black;
}

.wrapper-words {
	background-color: antiquewhite;
	padding: 10px;
	width: 29.7cm;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
}

:root {
	/* WORDS */
	--word-color: rgb(0, 0, 0);
	--word-color2: rgb(255, 255, 255);
	--mirrored-color: rgb(255, 255, 255);
	/* BGS */
	/* --bg-color: rgb(253, 253, 253); */
	--bg-color: rgba(255, 255, 255, 1);
	--bg-color-1: rgb(100, 81, 58);
	--bg-color-2: rgba(229, 199, 104, 255);
	--bg-color-3: rgb(204, 154, 94);
	--bg-color-4: rgba(255, 255, 255, 0);
	--bg-color-5: rgba(255, 255, 255, 0);
	--main-bg: rgba(147, 125, 59, 255);
	/* LINKERS */
	--blue-linker: rgb(23, 106, 248);
	--red-linker: rgb(248, 0, 23);
	--black-linker: rgb(103, 103, 103);
}

.word-card {
	width: 6.6cm;
	height: 4.4cm;
	background-color: var(--word-color);
	/* background-image: url(img/asohana.jpg);
	background-repeat: no-repeat;
	background-size: cover; */
	display: block;
	position: relative;
	background-blend-mode: color-burn;
	margin: 10px;
	display: flex;
	flex-direction: column;
	justify-content: flex-end;
	z-index: 1;
}
.word-card:after {
	content: '';
	/* background: url(img/voronoi_whited75.png); */
	background: url('img/hand-painted-watercolor-pastel-sky-cloud-background_41066-1948.avif');
	background-repeat: repeat;
	background-size: cover;
	background-blend-mode: darken;
	background-position: center;
	opacity: 0.95;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	position: absolute;
	z-index: 0;
}
.word-card span::before {
	content: '';
	background: url(img/swan.png);
	background-repeat: no-repeat;
	background-size: 10%;
	background-position: bottom right 1.5%;
	opacity: 0.2;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	position: absolute;
	z-index: 0;
}
