.word-card p {
	margin: 0 auto;
	padding: 0;
	font-family: 'AlegreSans-Regular' !important;
	letter-spacing: 0.1cm;
}

.word {
	/* font-weight: bold; */
	text-transform: uppercase;
	/* font-size: 0.7cm; */
	color: var(--word-color);
}

.word_unknown {
	font-weight: 500;
	text-transform: lowercase;
	font-size: 0.6cm;
	color: #000;
}

.unknown_list {
	text-align: left;
}

.mirrored {
	font-size: 0.5cm;
	color: var(--mirrored-color);

	text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000,
		1px 1px 0 #000, -1px -0.5px 0 #000, -0.5px -1px 0 #000, 1px 0.5px 0 #000,
		0.5px 1px 0 #000, -1px 0.5px 0 #000, 0.5px -1px 0 #000,
		1px -0.5px 0 #000, -0.5px 1px 0 #000, -1px 0.5px 0 #000,
		-0.5px 1px 0 #000, 1px -0.5px 0 #000, 0.5px -1px 0 #000,
		-1.5px 1px 0 #000;

	transform: rotate(180deg);
	padding: 0.1cm 0.6cm !important;
	background: linear-gradient(
		to right,
		var(--bg-color),
		var(--bg-color),
		var(--bg-color),
		var(--bg-color-5),
		var(--bg-color-4)
	);
	font-style: italic;
	z-index: 13;
	bottom: 2.7cm;
	width: 2.4cm;
	width: 100%;
	box-sizing: border-box;
	margin-left: -1.5cm !important;
	border-radius: 0 0 0 3cm;
	position: relative;
}

