.key-board {
	border: 5px solid #57473a;
	border-radius: 0.3cm;
	background: #000;
	padding: 0.3cm;
	width: 5.6cm;
	height: 5.6cm;
	margin: 0.12cm;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	align-content: center;
}

.clearfix:after {
	content: '';
	display: block;
	clear: both;
}

.cell {
	width: 0.96cm;
	height: 0.96cm;
	border-radius: 0.2cm;
	/* float: left; */
	margin: 0.05cm;
	box-shadow: inset 0px 0px 10px rgba(255, 255, 255, 0.5);
	position: relative;
}

.cell.blue {
	background: #90cfd4;
}

.cell.blue:after {
	content: '';
	display: block;
	/* position: absolute; */
	border-radius: 50%;
	border: 3px solid #118cc9;
	box-shadow: 0 0 10px #1895d4;
	width: 0.5cm;
	height: 0.5cm;
	background: #6abfc5;
	margin: 0.16cm auto;
}

.cell.red {
	background: #fdc2d6;
}

.cell.red:after {
	content: '';
	display: block;
	/* position: absolute; */
	border-radius: 8px;
	border: 3px solid #ec6d66;
	box-shadow: 0 0 10px rgb(255, 38, 38);
	width: 0.5cm;
	height: 0.5cm;
	background: #ffa1c0;
	margin: 0.16cm auto;
	transform: rotate(45deg);
}

.cell.white {
	background: #eed696;
}

.cell.Black {
	background: #000;
}

.cell.Black:after {
	content: '';
	display: block;
	width: 4px;
	height: 35px;
	transform: rotate(45deg);
	background: radial-gradient(#fff, #111);
	border-radius: 2px;
	left: 0.43cm;
	top: 0.025cm;
	position: absolute;
}

.cell.Black:before {
	content: '';
	display: block;
	width: 4px;
	height: 35px;
	transform: rotate(-45deg);
	background: radial-gradient(#fff, #111);
	border-radius: 2px;
	left: 0.43cm;
	top: 0.025cm;
	position: absolute;
}

.lamp {
	position: absolute;
	box-shadow: inset 0 0 10px rgba(255, 255, 255, 0.7);
	z-index: 1;
}

.lamp.red {
	background: #fdc2d6;
	background-image: radial-gradient(ellipse, #fb82ba, #fdc2d6, #fd6c9c);
}

.lamp.blue {
	background: #90cfd4;
	background-image: radial-gradient(ellipse, #82b5fb, #90cfd4, #28c4cf);
}

.lamp.top {
	top: 0.19cm;
	width: 1cm;
	height: 0.3cm;
	left: 3.35cm;
}

.lamp.blue.top {
	/* box-shadow: 0px 0px 15px #90cfd4; */
	box-shadow: 0px 0px 15px #0008ff;
}

.lamp.red.top {
	box-shadow: 0px 0px 15px #f00;
}

.lamp.bottom {
	bottom: 0.19cm;
	width: 1cm;
	height: 0.3cm;
	left: 3.35cm;
}

.lamp.blue.bottom {
	box-shadow: 0px -0px 15px #0008ff;
}

.lamp.red.bottom {
	box-shadow: 0px -0px 15px #f00;
}

.lamp.side {
	height: 1cm;
	width: 0.3cm;
	top: 3.35cm;
}

.lamp.right {
	right: 0.19cm;
}

.lamp.left {
	left: 0.19cm;
}

.lamp.blue.left {
	box-shadow: 0px 0px 15px #0008ff;
}

.lamp.blue.right {
	box-shadow: 0px 0px 15px #0008ff;
}

.lamp.red.left {
	box-shadow: 0px 0px 15px #f00;
}

.lamp.red.right {
	box-shadow: 0px 0px 15px #f00;
}

